const TagPage = ({ pageContext, data }) => {
   const { tag } = pageContext
   const { edges, totalCount } = data.allMdx
   
   const tagHeader = `${totalCount} project${
      totalCount === 1 ? "" : "s"
   } tagged with "${tag}"`

   return (
      <Layout>
         <article className="sheet">
            <h1 className="sheet__title">{Capitalize(tag)} Projects</h1>
            {/* <div className="sheet__inner">
            <h1 className="sheet__title">{Capitalize(tag)} Projects</h1>
               <div className="sheet__body">
                  <h3>{tagHeader}</h3>
                  <ul>
                  {edges.map(({ node }) => {
                     const { slug } = node.fields
                     const { title } = node.frontmatter
                     return (
                        <li key={slug}>
                        <Link to={`/${slug}`}>{title}</Link>
                        </li>
                     )
                  })}
                  </ul>
                  <Link to="/tags">All tags</Link>
               </div>
            </div> */}
         </article>
         <Masonry className="showcase">
         {data.allMdx.edges.map(({ node }) => (
            <div key={node.id} className="showcase__item">
               <figure className="card">
                  <Link to={`/${node.fields.slug}`} className="card__image">
                  <Img fluid={node.fields.thumbnail.childImageSharp.fluid} />
                  </Link>
                  <figcaption className="card__caption">
                     <h6 className="card__title">
                        <Link to={`/${node.fields.slug}`}>{node.frontmatter.title}</Link>
                     </h6>
                     <div className="card__description">
                        <p>{node.frontmatter.summary}</p>
                     </div>
                  </figcaption>
               </figure>
            </div>
         ))}
         </Masonry>
      </Layout>
   )
}

