export default TagPage

export const pageQuery = graphql`
   query($tag: String) {
      allMdx(
         filter: {frontmatter: {tags: {in: [$tag]}, published: {eq: true}, type: {eq: "project"}}},
         sort: {fields: [frontmatter___date], order: DESC}) {
         totalCount
         edges {
            node {
               frontmatter {
                  title
                  summary
               }
               fields {
                  slug
                  thumbnail {
                     childImageSharp {
                        fluid(maxWidth: 450) {
                           ...GatsbyImageSharpFluid
                        }
                     }
                  }
               }
            }
         }
      }
   }
`